@import "src/utilities/colors.scss";

.leader-board {
  &__table {
    width: 100%;
    margin-top: 6rem;
  }

  &__table-headings {
    font-size: 20pt;
    text-align: left;
  }

  &__button{
    font-size: 12pt;
    margin: 2rem 0;
    padding: 1rem;
    border: 2px solid $yellow;
    border-radius: 100px;

    &:hover{
      color: $blue;
      background-color: $yellow;
      border: 2px solid $blue;
      transition: 500ms;
    }
  }
}
